<template>
  <!-- DATA VIEW-->
  <div ref="datawrapper">
    <MDBContainer>
      <MDBRow>
        <MDBCol md="12" class="mx-auto">
          <MDBRow>
            <img class="mx-auto my-4" style="max-width: 500px" src="../assets/pollenyzer_logo.svg" />
          </MDBRow>
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <Transition appear name="bounce" @after-enter="showAnnotations(pollenData)">
          <p v-if="showCards[0]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardTitle>Pollen Detections</MDBCardTitle>
                <MDBCardText>
                  <ul style="margin-bottom: 2em">
                    <li>{{ pollenData?.pollen?.n_pollen }} pollen detected.</li>
                    <li><span>Pollen are highlighted below with <img src="@/assets/pollen_marker.svg"
                          style="width: 24px; vertical-align:middle;" />. The left arc shows the extracted primary
                        pollen color while the right
                        arc the secondary color.</span></li>
                    <li>Use the zoom function to check on the detections.</li>

                  </ul>

                  <div id="annotationimgcontainer" ref="annotationimgcontainer">
                  </div>
                  <img ref="annotationimg" id="annotationimg" style="display: none;">
                  <canvas style="display: none" ref="annotationcanvas" class="annotation-canvas"></canvas>
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
        <Transition appear name="bounce">
          <p v-if="showCards[1]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardTitle>Pollen Clusters</MDBCardTitle>
                <MDBCardText>
                  <ul style="margin-bottom: 2em">
                    <li>{{ pollenData?.pollen?.clusters?.n_clusters_predicted }} clusters and
                      {{ pollenData?.pollen?.clusters?.n_outliers_predicted }} outliers detected.</li>
                    <li>For all detected pollen the following dendrogram shows their attributed clusters in different colors. Connections
                      colored in light gray show outliers (that is clusters of size 1).</li>
                  </ul>
                  <img :src="pollenData?.pollen?.clusters?.dendrogram" style="width: 100%; max-width: 600px;" />
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
        <Transition appear name="bounce">
          <p v-if="showCards[2]">
            <MDBCard>
              <MDBCardBody>
                <MDBCardText>
                  <vue3-slider trackColor="#ffa820" color="#97be11" :tooltip="true" v-model="nCluster" :handleScale="3"
                    :min="1" :alwaysShowHandle="true" :sticky="true" :max="19" />
                  <div>
                    <div class="cluster-img-wrapper">
                      <div class="cluster-img">primary pollen colors per cluster</div>
                      <div class="cluster-img">secondary pollen colors per cluster</div>
                    </div>
                    <div v-for="(cluster, index) in pollenData?.pollen?.clusters?.cluster_imgs[nCluster]"
                      class="cluster-img-wrapper" :key="cluster">
                      <div
                        v-if="index > 1 && pollenData?.pollen?.clusters?.cluster_imgs[nCluster][index - 1].cluster_size > 1 && cluster.cluster_size == 1">
                        <div style="text-align: center; margin-bottom: -50px; color: #888;">outliers</div>
                        <hr class="outliers" />
                      </div>
                      <div style="color: #888">cluster {{ index + 1 }} (n={{ cluster.cluster_size }})</div>
                      <img :src="cluster.cluster_primary_canvas" class="cluster-img" />
                      <img :src="cluster.cluster_secondary_canvas" class="cluster-img" />
                    </div>
                  </div>
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </p>
        </Transition>
      </MDBRow>
    </MDBContainer>
    <CamFooter @nextCard="nextCard()" @previousCard="previousCard()" @redo="$emit('redo')" :prevDisabled="prevDisabled"
      :nextDisabled="nextDisabled" :redoDisabled="false" />
  </div>
</template>

<style>
hr.outliers {
  border-top: 5px dotted #888;
  color: #fff;
  background-color: #fff;
  margin-bottom: 5px;
  margin-top: 50px;
}

.vue3-slider {
  margin-bottom: 40px;
}

.cluster-img-wrapper {
  text-align: center;
}

.cluster-img {
  image-rendering: pixelated;
  width: 30%;
  max-width: 200px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5%;
  display: inline-block;
  font-weight: bold;
}

.annotation-canvas {
  width: 100%;
}

.bar-canvas {
  width: 100%;
  height: 70%;
}

.card-body {
  padding-bottom: 70px;
}
</style>

<script>
/* eslint-disable */
import {
  MDBCol, MDBRow, MDBContainer, MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
} from "mdb-vue-ui-kit";
import CamFooter from '@/components/CamFooter.vue'
import slider from "vue3-slider"
import Zoomist from 'zoomist'


export default {
  components: {
    MDBCol,
    MDBRow,
    MDBContainer,
    MDBCard,
    MDBCardBody,
    MDBCardTitle,
    MDBCardText,
    CamFooter,
    "vue3-slider": slider,
  },
  props: {
    pollenData: Object,
  },
  data() {
    return {
      showCards: [true, false, false],
      cardId: 0,
      nCluster: 1,
      prevDisabled: true,
      nextDisabled: false,
    }
  },
  mounted() { },
  watch: {
    pollenData(value, oldValue) {
      this.showAnnotations(value)
    }
  },
  methods: {
    showAnnotations(pollenData) {
      if (pollenData) {
        this.nCluster = (
          this.pollenData.pollen.clusters.n_clusters_predicted +
          this.pollenData.pollen.clusters.n_outliers_predicted
        )
        let annotationCanvas = this.$refs.annotationcanvas
        let annotationImg = this.$refs.annotationimg
        const context = annotationCanvas.getContext('2d');
        context.canvas.width = pollenData.pollen.img_width
        context.canvas.height = pollenData.pollen.img_height
        var image = new Image();
        image.onload = function () {
          context.drawImage(image, 0, 0, pollenData.pollen.img_width, pollenData.pollen.img_height);

          context.beginPath();
          context.lineWidth = 10;
          context.strokeStyle = "#ffa820";
          context.rect(
            pollenData.pollen.annotations.rect.upperleft_x,
            pollenData.pollen.annotations.rect.upperleft_y,
            pollenData.pollen.annotations.rect.width,
            pollenData.pollen.annotations.rect.height,
          );
          context.stroke();

          const lineWidth = pollenData.pollen.dimension

          for (let i = 0; i < pollenData.pollen.annotations.coords.length; i++) {
            let coord = pollenData.pollen.annotations.coords[i]
            let primary_color = pollenData.pollen.annotations.str_colors_primary[i]
            let secondary_color = pollenData.pollen.annotations.str_colors_secondary[i]
            const centerX = coord.x;
            const centerY = coord.y;
            const radius = pollenData.pollen.dimension * 2

            console.log("sdfdsf", centerX, centerY, radius)
            context.beginPath();
            context.arc(centerX, centerY, radius, Math.PI / 2, Math.PI * 3 / 2, false);
            //context.fillStyle = primary_color;
            //context.fill();
            context.lineWidth = lineWidth;
            context.strokeStyle = primary_color;
            context.stroke();
            context.beginPath();
            context.arc(centerX, centerY, radius, Math.PI / 2, Math.PI * 3 / 2, true);
            //context.fillStyle = secondary_color;
            //context.fill();
            context.lineWidth = lineWidth;
            context.strokeStyle = secondary_color;
            context.stroke();
            context.beginPath();
            context.arc(centerX, centerY, radius - lineWidth / 2, 0, 2 * Math.PI, false);
            context.lineWidth = Math.max(1, lineWidth / 2);
            context.strokeStyle = "#dc1e0b";
            context.stroke();
          }
          annotationImg.src = annotationCanvas.toDataURL()
          new Zoomist("#annotationimgcontainer", {
            src: annotationImg,
            maxRatio: 4,
            slider: false,
            zoomer: true,
          })
        };
        image.src = pollenData.dataURL
      }
    },
    nextCard() {
      this.showCards[this.cardId] = false;
      this.cardId++;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 2) {
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
    },
    previousCard() {
      this.showCards[this.cardId] = false;
      this.cardId--;
      this.showCards[this.cardId] = true;
      if (this.cardId === 0) {
        this.prevDisabled = true;
        this.nextDisabled = false;
      } else if (this.cardId === 2) {
        this.prevDisabled = false;
        this.nextDisabled = true;
      } else {
        this.prevDisabled = false;
        this.nextDisabled = false;
      }
    },
  },
};
</script>


